body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  height: 5000px;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  height: 100%;
  width: 95%;
  margin: 0 auto;
}

.navigation {
  z-index: 999;
  position: fixed;
  height: 80px;
  width: 100%;
  background: #03529b;
  box-shadow: 0 0 30px 20px rgba(0, 0, 0, 0.25);

  .nav_main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;

    .nav_logo {
      width: 260px;

      a img {
        width: 90px;
        height: auto;
      }
    }

    .nav_menu {
      ul {
        display: flex;
        gap: 10vw;

        li a {
          color: white;
          text-transform: uppercase;
          font-weight: 100;
          font-size: 20px;
          white-space: nowrap;
        }
      }
    }

    .nav_lang {
      width: 260px;
    }
  }
}

header {
  position: relative;
  overflow: hidden;

  .container {
    display: flex;
    align-items: center;
    justify-content: center;

    h1 {
      color: white;
      font-size: 45px;
      position: absolute;
      font-family: 'Raleway', sans-serif;
      font-weight: 200;
      text-align: center;

      span {
        font-size: 50px;
      }
    }
  }


  .header_right_hand {
    position: relative;
    top: 0;
    left: 55%;

    .img_right_hand {
      transform: rotate(-10deg);
    }

    .header_heart {
      margin-left: 13%;
      margin-top: 10%;
      position: absolute;

      .img_heart {
        user-select: none;
        -webkit-user-drag: none;
        transition: transform 3.5s cubic-bezier(0.31, 0.35, 0.09, 0.96);
        transform: rotate(-115deg);
        width: 10vw;
        height: auto;
      }

      .img_heart.fade {
        transform: rotate(-115deg) translate(5vw, 45vh);
      }
    }
  }

  .header_left_hand {
    transform: scaleX(-1);
    position: relative;
    top: 60%;
    right: 55%;

    .img_left_hand {
      transform: rotate(25deg);
    }
  }

  .img_hand {
    user-select: none;
    -webkit-user-drag: none;
    position: absolute;
    width: 60vw;
    height: auto;
    transition: transform 3s cubic-bezier(0.31, 0.35, 0.09, 0.96);
  }

  .img_right_hand.fade {
    transform: rotate(-10deg) translate(15vw, -10vh);
  }

  .img_left_hand.fade {
    transform: rotate(25deg) translate(15vw, 10vh);
  }

  height: 100vh;
  width: 100%;
  background: rgb(1, 86, 165);
  background: -moz-linear-gradient(45deg, rgba(1, 86, 165, 1) 0%, rgba(39, 101, 177, 1) 100%);
  background: -webkit-linear-gradient(45deg, rgba(1, 86, 165, 1) 0%, rgba(39, 101, 177, 1) 100%);
  background: linear-gradient(45deg, rgba(1, 86, 165, 1) 0%, rgba(39, 101, 177, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#0156a5", endColorstr="#2765b1", GradientType=1);
}

.centres {
  overflow: hidden;
  height: 450px;
  width: 100%;
  background: lightblue;

  .centres_wrap {
    display: flex;
    justify-content: center;
    height: 100%;

    .centres_box {
      position: relative;
      min-width: 70vw;
      height: 100%;
      transform: skewX(-20deg);
      overflow: hidden;

      .centres_inner {
        position: absolute;
        transform: skewX(20deg);
        width: 115%;
        height: 100%;
        left: -10%;
        background: url("../resources/images/centre.jpg") center center /cover ;
      }
    }
  }
}